
/*引入top10列表的样式*/
@import "./styles/top10.css";

html {
  height: 100%;
}

body {
  background: url(images/body-bg.jpg) no-repeat;
  background-size: 100% 100%;
  width: 1920px;
  height: 1080px;
  color: #fff;
  font-family: '微软雅黑';
}

@font-face {
  font-family: 'digifacewide';
  src: url('../../loader/common/fonts/digifacewide.ttf');
}

@font-face {
  font-family: '微软雅黑';
  src: url('../../loader/common/fonts/msyh.ttf');
}

.dynamic-header {
  height: 40px;
  position: relative;
  .top-title {
    position: absolute;
    top: 6px;
    left: 783px;
    height: 30px;
    width: 350px;
  }
  .header-info {
    font-size: 14px;
    position: absolute;
    right: 65px;
    top: 2px;
    .current-time {
      color: #00ffff;
      font-size: 16px;
      height: 24px;
      line-height: 24px;
    }
    p {
      float: left;
      line-height: 36px;
      margin-top: 10px;
      color: #d5d5d5;
    }
    .user {
      padding-right: 25px;
    }
    .out:hover {
      color: #fff;
      cursor: pointer;
    }
    i {
      width: 13px;
      height: 13px;
      display: inline-block;
      color: #fff;
    }
    i.user-icon {
      background: url(images/user-icon.png) no-repeat;
    }
    i.out-icon {
      background: url(images/out-icon.png) no-repeat;
    }
  }
}

.dynamic-templates {
   position: relative;
   top: 50px;
   left: 100px;
   height: 920px;
   width: 1720px;
  .pages-left {

  }
  .value {
    color: #00fefe;
  }
  /*左边*/
  .pages-left {
    float: left;
    width: 787px;
    .in-network {
      .terminal,
      .peopleNumer {
        float: left;
        background: url(images/inNetwork-bg.png) no-repeat;
        width: 383px;
        height: 163px;
      }
      .title {
        width: 170px;
        text-align: center;
        line-height: 45px;
        margin-left: 28px;
      }
      .number {
        font-size: 58px;
        color: #00ffff;
        text-shadow: 3px 3px 18px #00a9ab;
        width: 304px;
        height: 70px;
        line-height: 70px;
        text-align: center;
        padding-left: 50px;
      }
    }
      .phone-number,
      .in-network {
        float: left;
        width: 100%;
      }
      /*在渝、出渝手机数量统计*/
      .phone-number {
        .count {
          width: 100%;
          float: left;
        }
        /*在渝手机数量统计*/
        .in-count {
          margin-top: 10px;
          .count-list {
            background: url(images/in-yu-count-bg.png) no-repeat;
            width: 786px;
            height: 148px;
            margin-bottom: 35px;
            li {
              width: 225px;
              height: 150px;
              margin-left: 14px;
              color: #bbcad2;
              &:first-child {
                margin: 0;
              }
            }
            .value {
              font-size: 32px;
              padding-left: 110px;
              margin-top: -10px;
              width: 140px;
            }
          }
        }
        /*出渝手机数量统计*/
        .out-count {
          margin-top: 10px;
          .count-list {
            background: url(images/out-yu-count-bg.png) no-repeat;
            width: 786px;
            height: 170px;
            margin-bottom: 35px;
            li {
              width: 355px;
              height: 155px;
              padding-right: 25px;
              margin-left: 25px;
              &:first-child {
                margin: 0;
              }
            }
            
            .value {
              font-size: 38px;
              margin-top: 10px;
            }
          }
        }
        .title {
          background: url(images/title-bg.png) no-repeat;
          width: 759px;
          height: 57px;
          padding-left: 28px;
        }
      }
  }
  .pages-right {
    position: absolute;
    right: 0px;
    width: 910px;
    height: 635px;
    border: 1px solid #138bce;
  }
  .pages-left,
  .pages-bottom {
    .title {
      font-size: 20px;
      color: #fff;
      line-height: 57px;
    }
  }
  .pages-bottom {
    position: absolute;
    bottom: 0px;
    background: url(images/controlTaskCout-bg.png) no-repeat;
    width: 1720px;
    height: 220px;
    background-size: 100% 100%;
    float: left;
    .title {
      line-height: 44px;
      margin-left: 36px;
      width: 286px;
      height: 50px;
      span {
        font-size: 38px;
        color: #00ffff;
        margin-left: 30px;
        padding-top: -8px;
        float: right;
        display: inline-block;
      }
    }
    .count-list {
      li {
        background: url(images/count-bg.png) no-repeat;
        width: 173px;
        height: 90px;
        margin: 35px 125px 0 0;
        font-size: 18px;
        padding-top: 5px;
        width: 120px;
        p:first-child {
          width: 138px;
          text-align: center;
          margin-left: 15px;
        }
        &:first-child {
          margin-left: 100px;
        }
      }
      .value {
        font-size: 38px;
        margin-top: 17px;
        width: 110px;
      }
      .warning-number .value,
      .total-person .value {
        width: 200px;
      }
      div.value {
        font-size: 38px;
        margin-top: -3px;
        margin-left: 20px;
      }
    }
  } 
  .count-list {
    li {
      float: left;
      text-align: right;
      padding: 45px 25px 0 0;
      font-size: 18px;
    }
  }
}

/*显示所有地区名字*/
.show-all-name {
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(5, 45, 104, 0.8);
  padding: 10px;
  border-radius: 2px;
  z-index: 10;
  display: none;
}
 


