.out-cq-top {
    position: absolute;
    top: 0;
    right: 0;
}

.top10-container {
    position: relative;
    /*top: 100px;
    left: 100px;*/
    font-family: 微软雅黑;
    .nav-group {
        height: 27px;
         .nav {
            float: left;
            width: 116px;
            height: 27px;
            margin-top: 3px;
            text-align: center;
            color: #b7b7b7;
            font-size: 14px;
            line-height: 27px;
            cursor: pointer;
            background-image: url(../images/top10/nav-bg.png);
            background-repeat: no-repeat;
            &.active {
                color: #00ffff;
                background-image: url(../images/top10/nav-active-bg.png);
            }
            &:nth-child(1) {
                margin-left: 20px;
            }
            &:not(:first-child) {
                margin-left: 5px;
            }
         }
    }
    .top10-title {
        position: absolute;
        width: 446px;
        height: 56px;
        color: #ffffff;
        font-size: 20px;
        text-indent: 1em;
        line-height: 56px;
        background-image: url(../images/top10/top-title.png);
        background-repeat: no-repeat;
        .top-details {
            float: right;
            color: #e5e5e5;
            font-size: 12px;
            margin-right: 20px;
            &:hover{
                color:#fff;
                cursor: pointer;
            }
        }

    }
    .list-container {
        position: relative;
        top: 56px;
        width: 446px;
        height: 560px;
        background-image: url(../images/top10/list-bg.png);
        background-repeat: no-repeat;
        .list-title {
            position: relative;
            top: 25px;
            color: #00ffff;
            font-size: 18px;
            a {
                display: inline-block;
                text-align: center;
                &:nth-child(1) {
                    width: 110px;
                }
                &:nth-child(2) {
                    width: 100px;
                }
                &:nth-child(3) {
                    width: 210px;
                }
            }
        }
        .top10-list {
            position: relative;
            top: 65px;
            color: #e5e5e5;
            font-size: 17px;
            li {
                height: 40px;
                a {
                    display: inline-block;
                    span {
                        font-size: 14px;
                    }
                    i {
                        color: #b6c0ca;
                        font-size: 36px;
                        float: left;
                        width: 11px;
                        display: inline;
                        margin-left: 50px;
                        font-family: '微软雅黑';
                    }
                    .equal {
                        margin-top: -13px;
                    }
                    .up-icon {
                        display: inline-block;
                        height: 18px;
                        background-image: url(../images/top10/up.png);
                    }
                    .down-icon {
                        display: inline-block;
                        height: 18px;
                        background-image: url(../images/top10/down.png);
                    }
                    &:nth-child(1) {
                        width: 120px;
                        text-indent: 1em;
                        text-align: left;
                        text-indent: 2em;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    &:nth-child(2) {
                        width: 130px;
                        text-indent: 1em;
                        font-family: digifacewide;
                    }
                    &:nth-child(3) {
                        width: 125px;
                        text-align: right;
                        color: #e08a26;
                        font-family: digifacewide;
                    }
                }
            }
        }
    }
}
/*暂无数据*/
.list-no-data {
    font-size: 24px;
    text-align: center;
    margin-top: 140px;
}

/*top 查看详情*/
.top-detials-dialog {
    position: absolute;
    top: 0px;
    background: rgba(0, 0, 0, 0.9);
    width: 100%;
    height: 1080px;
    display: none;
    .close {
        font-size: 32px;
        color: #fff;
        background: #162142;
        border-radius: 50%;
        height: 40px;
        width: 40px;
        text-align: center;
        line-height: 40px;
        position: absolute;
        right: 20px;
        top: 20px;
        cursor: pointer;
    }
    .detials-content {
        background: url(../images/deitals-bg.png) no-repeat;
        width: 540px;
        height: 943px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -270px;
        margin-top: -471.5px;
        .search {
            padding-top: 10px;
            float: left;
            input {
                outline: none;
                background: none;
                float: left;
                border: none;
            }
            input[type='text'] {
                width: 390px;
                height: 40px;
                font-size: 16px;
                color: #e7e7e7;
                line-height: 40px;
                padding-left: 15px;
            }
            input[type='button'] {
                width: 26px;
                height: 40px;
                margin: 5px 0 0 10px;
                cursor: pointer;
            }
        }
        .top-title {
            font-size: 24px;
            color: #ffffff;
            width: 528px;
            height: 66px;
            line-height: 56px;
            float: left;
            margin-top: 30px;
            padding-left: 20px;
        }
        .search-container {
            float: left;
            margin-top: 35px;
            padding-left: 35px;
            width: 510px;
            .list-container {
                float: left;
                font-size: 25px;
                .list-title {
                    float: left;
                    width: 100%;
                    
                    color: #00ffff;
                    li {
                        float: left;
                        &:nth-child(1) {
                            width: 130px;
                        }
                        &:nth-child(2) {
                            width: 135px;
                            
                        }
                        &:nth-child(3) {
                            width: 210px;
                        }
                    }

                }
                .search-list {
                    height: 607px;
                    overflow-y: auto;
                    width: 99%;
                    margin-top: 40px;
                    float: left;
                    li{
                        height: 40px;
                        line-height: 40px;
                        a{
                            display: inline-block;
                            float: left;
                            &:nth-child(1) {
                                width: 120px;
                                text-align: left;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                            }
                            &:nth-child(2) {
                                width: 155px;
                                padding-left: 15px;
                                font-family: digifacewide;
                            }
                            &:nth-child(3) {
                                width: 165px;
                                text-align: center;
                                font-family: digifacewide;
                                color: #e08a26;
                            }
                            i {
                                color: #b6c0ca;
                                font-size: 36px;
                                float: left;
                                width: 11px;
                                display: inline;
                                margin-left: 50px;
                                padding-right: 10px;
                                margin-top: 12px;
                                font-family: '微软雅黑';
                            }
                            .equal {
                                margin-top: -1px;
                            }
                            .up-icon {
                                height: 18px;
                                background: url(../images/top10/up.png) no-repeat;
                            }
                            .down-icon {
                                height: 18px;
                                background: url(../images/top10/down.png) no-repeat;
                            }
                        }
                    }
                }
            }
        }
    }
    .close {
        background: url(../images/close.png) no-repeat;
        width: 23px;
        height: 23px;
        position: absolute;
        right: 20px;
        top: -5px;
    }
}

.search-list::-webkit-scrollbar {
    width: 3px;
    height: 6px;
}

.search-list::-webkit-scrollbar-track: {
    background-color: #fff;
}

.search-list::-webkit-scrollbar-thumb {
    background-color: #00f9f2;
    border-radius: 20%;
}

.search-list::-webkit-scrollbar-button {
    background-color: #00f9f2;
    display: none;
}

.search-list::-webkit-scrollbar-corner {
    background-color: black;
}


